.largeNavContainer {
    display: flex;
    flex-direction: column;
    width: 134px;
    padding-top: 72px;
    gap: 40px;
    position: sticky;
    top: 0;
    left: 0;
}

.largeNavList {
    display: flex;
    width: 100%;
    flex-direction: column;
    align-items: flex-start;
    gap: 16px;
}

.largeNavTitle {
    font-family: "Plantin MT Pro";
    font-size: 18px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    letter-spacing: -1.8px;
    color: #2519D2;
}